import { Injectable } from '@angular/core';
import { BaseLinkProvider } from '../providers/base-link/base-link';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { FirebaseMessagingProvider} from '../providers/firebase-messaging/firebase-messaging';
import { UserService } from '../providers/user-service';
import { Firebase } from '@ionic-native/firebase';


// Observable class extensions
import 'rxjs/add/observable/of';
import 'rxjs/add/observable/throw';

// Observable operators
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/switchMap';


/*
  Generated class for the CommentService provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular 2 DI.
*/
@Injectable()
export class CommentService {


  headers: Headers;

  options: RequestOptions;

  constructor(
    private _http: Http,
    private userService: UserService,
    private messaging: FirebaseMessagingProvider
  ) {
    this.headers = new Headers({
      'Content-Type': 'application/json',
      'Accept': 'q=0.8;application/json;q=0.9'
    });
    this.options = new RequestOptions({ headers: this.headers });
  }

  private _url = BaseLinkProvider.GetBaseUrl() + '/Comment';

  public GetStoryComments(storyID: number) {
    return this._http.get(this._url + '?storyID=' + storyID)
      .map(ret => ret.json());
  }

  public PostComment(storyID: number, userID: number, comment: string): Observable<any> {

    this.messaging.SubscibeToTopic(storyID.toString());

    return this._http.post(this._url,
      JSON.stringify({
        StoryID: storyID,
        Comments: comment,
        User: {
          ID: userID
        }
      }),
      { headers: this.headers })
      .map(res => res.json())
      ._catch(this.handleError);
  }

  private handleError(error: any) {
    let errMsg = (error.message) ? error.message :
      error.status ? `${error.status} - ${error.statusText}` : 'Server error';
    console.error(errMsg);
    console.log(error._body);
    return Observable.throw(errMsg);
  }

}



